 

.App {
  text-align: center;   
}   

* {
  overflow: overlay;
}

.App-header { 
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; 
}

.navOptions li a {
  color: white; 
  text-decoration: none;
  padding: 20px 0;
}

.app nav { 
  background-color: #1e212798;  
  position: fixed;
  width: 100%;
  z-index: 10;
  backdrop-filter: blur(9px);
}

.app ul {
  list-style-type: none;
  overflow: auto;
  clear: both;
  margin: 0 20px 0 0;
  padding: 0;
}  
.app li:hover {  
  background-color: #282c34b4;
  cursor: pointer;
  transition:cubic-bezier(1, 0, 0, 1);
}

.navOptions li {
  float: right;   
  padding: 20px 0;
} 

.navOptions li a { 
  padding: 20px 15px;
}

.app header {
  background-image: url('./images/PhotoMyrtleBeach.com-7.jpg');
  background-position: center, center;
  background-size: cover;
  box-shadow: rgba(0, 0, 0, 0.35) 0px -50px 36px -28px inset;  
}

.app header div {
  background-image: linear-gradient(to bottom, transparent, rgba(29, 29, 29, 0.253));
  background-position: center;
  border-radius: 5px;
  border: none;
  text-align: center;
  padding: 10px;    
}    

::-webkit-scrollbar {  
  width: 12px;   
}  

::-webkit-scrollbar-thumb { 
  background: #0d47a1; 
  border-radius: 2px; 
 }  

#title {
  text-align: center;
  font-size: 50px;
}
 
.carousel { 
  padding: 20px 0 100px 0;;
}

#unity {
  margin: 100px 0 10px 0;
}

#unity >* {
  transition: .5s;
}
 
#unity #title {
  margin: 0 0 50px 0;
}

#unity-description {
  text-align: center;
  font-weight: 600;
}

.scrollToTop {
  position: fixed;
  z-index: 10; 
  bottom: 0;
  right: 0;
  margin: 0 50px 35px 0;
  padding: 10px 15px 10px 15px;
  color: white;
  border: none;
  border-radius: 3px;
  background: linear-gradient(to bottom, rgba(1, 1, 1, 0.411), rgba(1, 1, 1, 0.411));
  backdrop-filter: blur(8px);
  transition: 0.5s;
}

.scrollToTop:hover {
  background: linear-gradient(to bottom, rgba(1, 1, 1, 0.568), rgba(1, 1, 1, 0.836));
  transition: 0.5s;
}

.scrollToTop:active {
  background: linear-gradient(to bottom, rgba(1, 1, 1, 0.842), rgb(1, 1, 1));
  transition: 0.5s;
}